.App {
  text-align: center;
  margin-top:7%;
  
}
.App-header{
  color:white;
  position:static;
  background:black;
}
.App-logo {
 
  height: 40vmin;
  pointer-events: none;
}
.header-title{
  padding-bottom:150px;
  background: url('../img/sneakers-background.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  position:initial;
  border: 30px solid black;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: minmax(900px, auto);
  grid-template-areas:
  ". leftlogo hd hd hd hd hd hd hd hd rightlogo ."
}
.container{
  padding:50px;
  background: #121212;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "sadlogo sadlogo text-container text-container text-container text-container"
}
.container-2{
  padding:50px;
  background: white;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "tc2 tc2 tc2 tc2 happylogo happylogo"
}
.container-3{
  padding:50px;
  background: #121212;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "templogo1 tc3 tc3 tc3 tc3 templogo"
}

.text-container{
  grid-area:text-container;
}
.text-container h1{
  padding: 50px;
  font-size:50px;
}
.text-container-2{
  grid-area:tc2;
  margin-top:10%;
}
.text-container-2 h1{
  padding: 50px;
  font-size:50px;
  color:black;
}
.text-container-3{
  grid-area:tc3;
  margin-top:10%;
}
.text-container-3 h2{
  border: 10px solid white;
  margin:20px;
  padding: 50px;
  color:white;
  padding:50px;
}
.templogo{
  grid-area:templogo;
  margin-top: 50%;
}
.templogo1{
  grid-area:templogo1;
  margin-top:50%;
}
.templogo img{
  margin-top:80%;
  background: #121212;
  max-width: 300px;
  max-height: 300px;
  border-radius: 90%;
}
.templogo1 img{
  margin-top:80%;
  background: #121212;
  max-width: 100%;
  max-height: 50%;
  border-radius:90%;
}
.leftlogo{
  grid-area:leftlogo; 
}
.leftlogo img{
  background: white;
  width: 100%;
  height: 50%;
  margin-top:75%;
  border-radius: 100%;

}
.rightlogo{
  grid-area:rightlogo;
}
.rightlogo img{
  background: white;
  width:100%;
  height:50%;
  margin-top:75%;
  border-radius: 100%;
  
}
.sadlogo{
  grid-area:sadlogo;
  margin-top: 50%;
}
.sadlogo img{
  background: #121212;
  max-width: 500px;
  max-height: 500px;
}
.hd{
  grid-area:hd;
  margin-top:12%;
  font-size:100%;
}
.hd h1{
  text-align:center;
  text-decoration: line-through black;
  border: 5px solid black;
  margin-left:25%;
  margin-right:25%;
  font-size:50px;
}
.happylogo{
  grid-area:happylogo;
  margin-top:50%;
}
.happylogo img{
  background: white;
  max-width: 500px;
  max-height: 500px;
}



@media (prefers-reduced-motion: no-preference) {
  .logo-motion{
    animation: App-logo-spin infinite 8s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .happy-logo-motion {
    animation: Happy-logo-spin infinite 20s linear;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .sad-logo-motion {
    animation: Sad-logo-spin infinite 30s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Happy-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Sad-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}