.NavigationBar{
    position:fixed;
    top:0px;
   
    border:5px solid black;
    background: white;
    width:100%;
    display:grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: minmax(50px, auto);
    grid-template-areas:
    "logo-img title-text title-text title-text . . . .home contact signin login"

}
.NavigationBar h1{
    color:black;
    /*transform: translate(10%, 10%);*/
}
.title-text{
    grid-area:title-text;
    padding:5% 10%;
    margin: 10px 20px;
}
.title-text h1{
    text-align: center;
    border: 5px solid black;
}
/*.rightside-btn{
    grid-area: rightside-btn; 
}*/
.home-btn{
    grid-area:home;
}
.contact-btn{
    grid-area:contact;
}
.signin-btn{
    grid-area:signin;
}
.login-btn{
    grid-area:login;
}

a:hover {
    color: white;
    background-color: black; 
}
a:hover a{
    color:white;
}

a{

    font-weight: bold;
    text-decoration: none;
    text-shadow: bold;
    color:black;
    width:100%;
    background: white;
    text-decoration:none;
    text-align: center;
    font-size:30px;
    padding-top:35%;
}

.logo-img{
    margin:20px 20px;
    padding:10px;
    grid-area:logo-img;
    width: 80%;
    height: 80%;
}

@media (prefers-reduced-motion: no-preference) {
    .logo-motion{
      animation: App-logo-spin infinite 8s linear;
    }
}
@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}